.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	@include max-lg {
		height: 100vh;
	}
	@include max-md {
		height: auto;
	}
	.scroll-icon {
		position: relative !important;
		z-index: 10;
	}
	@include max-md {
		.elements-absolute {
			width: 120%;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 280px;
		@include max-md {
			width: 45%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 280px;
		@include max-md {
			width: 45%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translate(-20%, 0%);
		@include max-lg {
			.image-element {
				position: relative;
				// transform: translateX(-50%);
			}
		}
		@include max-md {
			width: 220px;
		}
		.orn-5 {
			position: absolute;
			left: 1rem;
			top: 0;
			width: 160px;
			transform: translate(20%, -50%);
			@include max-lg {
				left: 0;
				transform: translate(20%, -50%);
			}
			@include max-md {
				width: 100px;
				z-index: 5;
			}
		}
		.orn-6 {
			position: absolute;
			left: 0;
			top: 0;
			transform: translate(80%, -80%);
			width: 80px;
			@include max-lg {
				width: 100px;
				transform: translate(60%, -100%);
			}
			@include max-md {
				width: 40px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 3rem;
		right: 0;
		width: 320px;
		@include max-md {
			width: 220px;
			bottom: 0;
		}
		.image-element {
			transform: translateX(50%);
		}
		img {
			position: relative;
		}
		.orn-5 {
			position: absolute;
			right: 0;
			top: 0;
			width: 160px;
			transform: translateY(90%);
			@include max-lg {
				width: 120px;
				transform: translateY(100%);
			}
			@include max-md {
				width: 80px;
				transform: translateY(130%);
			}
		}
		.orn-6 {
			position: absolute;
			right: 0;
			top: 0;
			transform: translateY(-80%) scaleX(-1);
			width: 80px;
			@include max-lg {
				width: 100px;
			}
			@include max-md {
				width: 40px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 40%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		border-radius: 500px;
		border: 1px solid #ecdcc2;
		padding: 8px;
		@include min-xxl {
			width: 50%;
		}
		@include max-lg {
			width: 60%;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.background-header {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px;
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		.header {
			position: relative;
			display: block;
			width: 480px;
			margin: 3.5rem 0 0;
			padding: 5rem 0.5rem;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					padding: 1rem 0 0;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
